import React from "react";
import { useContext } from 'react';
import GooglePhotosContext from "../../Providers/googlePhotoContext";
import {GatsbyImage, getImage} from "gatsby-plugin-image";

import PhotoBoard from "../PhotoBoard";

export function GooglePhotosAlbum(props) {
    const photos = useContext(GooglePhotosContext);
    const {keyword, albumtitle} = props;

    const sanitizedKeyword = keyword.trim().replaceAll('\\', '');
    return (
        <PhotoBoard>
            {photos.filter(photoNode => photoNode.description === sanitizedKeyword).map(photoNode => {
                return (
                    <GatsbyImage 
                        key={photoNode.id} 
                        image={getImage(photoNode.file)} 
                        alt={albumtitle}
                    />
                );
            })}
        </PhotoBoard>
    );
}

export default GooglePhotosAlbum;